@import"https://fonts.googleapis.com/css2?family=Lora&display=swap";

.sub{
    width : 90%;
    height : auto;
    border-bottom : 1px solid black;
    margin : 0 auto;
}

.head{
    
    font-family: 'Lora', serif;
    font-size: 1.2em;
    line-height: 40px;
    color : #0033c7;
}

@media (max-width: 600px){
    .sub{
        height: auto;
    }
    .head{
        
        line-height: 30px;
        font-size : 15px;
    }
}